import path from 'path';
import { InlineConfig, mergeConfig } from 'vite';
import { withoutVitePlugins } from '@storybook/builder-vite';
import { default as react } from '@vitejs/plugin-react';
import { jsonStringifyAndEscapeForInlineJS } from '@hubspot/cms-js-internal';

const webRoot = process.env.WEB_ROOT;
const storyPath = path.resolve(process.cwd(), webRoot, '**/*.stories.*');
const jsRenderingGates = process.env.GATES ? process.env.GATES.split(',') : [];

export default {
  addons: ['@storybook/addon-essentials'],
  framework: '@storybook/react-vite',
  stories: [storyPath],
  previewHead: (head: string) => {
    return `
    ${head}

    <script type="module">
      import { initConfigSingletonFromJSON } from '@hubspot/cms-components/config';

      initConfigSingletonFromJSON(${jsonStringifyAndEscapeForInlineJS(
        {
          portalId: process.env.portalId,
          hublet: process.env.hublet,
          hsEnv: process.env.hsEnv,
          hsDeployed: false,
          hsJSRGates: jsRenderingGates,
        },
        { prettyPrint: false }
      )})
    </script>

  `;
  },
  viteFinal: async (config: InlineConfig) => {
    const { resolveIslandPlugin } = await import('@hubspot/cms-js-internal');

    const viteConfig = {
      resolve: {
        dedupe: [
          'react',
          'react-dom',
          '@hubspot/cms-components',
          '@hubspot/cms-dev-server',
        ],
      },
      optimizeDeps: {
        include: ['react', 'react-dom', 'react-dom/client'],
        exclude: ['fsevents'],
      },
      css: {
        postcss: webRoot,
      },
      plugins: [
        resolveIslandPlugin(),
        react({
          babel: {
            plugins: [
              'babel-plugin-styled-components',
              'styled-jsx/babel',
              'macros',
            ],
          },
        }),
      ],
    };

    const configNoReact: InlineConfig = {
      ...config,
      // replacing the default usage of `@vite/plugin-react` (it emits multiple
      // sub-plugins listed here) to add our babel plugin configuration
      plugins: await withoutVitePlugins(config.plugins, [
        'vite:react-babel',
        'vite:react-swc',
        'vite:react-refresh',
      ]),
    };

    return mergeConfig(configNoReact, viteConfig);
  },
};
